<script>
export default {
	data() {
		return {
			userInfo: {
				avatar: '/static/default_avatar.png',
				name: 'Áî®Êà∑',
				id: '',
				level: 'ÂÅ•Ë∫´Êñ∞Êâã',
				totalDays: 0,
				totalCalories: 0,
				totalMinutes: 0,
				age: 25,
				height: 170,
				weight: 65,
				gender: 'Áî∑',
				dailyCalorieGoal: 600,
				dailyExerciseGoal: 30,
				dailyStepGoal: 10000
			},
			appSettings: {
				notifications: true,
				privacy: true
			},
			isLoading: false
		};
	},
	onShow() {
		this.loadUserInfo();
		this.loadUserStats();
	},
	methods: {
		// Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØ
		loadUserInfo() {
			const token = uni.getStorageSync('token');
			const storedUserInfo = uni.getStorageSync('userInfo');
			
			if (storedUserInfo) {
				this.userInfo = { ...this.userInfo, ...storedUserInfo };
			}
			
			if (!token) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				});
				return;
			}
			
			// ‰ªéÂêéÁ´ØËé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
			uni.request({
				url: 'http://localhost:8088/api/users/me',
				method: 'GET',
				header: {
					'Authorization': 'Bearer ' + token
				},
				success: (res) => {
					if (res.data.code === 0) {
						const userData = res.data.data;
						this.userInfo = {
							...this.userInfo,
							...userData,
							avatar: userData.avatar || '/static/default_avatar.png'
						};
						// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
						uni.setStorageSync('userInfo', this.userInfo);
					}
				},
				fail: () => {
					console.log('Âä†ËΩΩÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•Ôºå‰ΩøÁî®Êú¨Âú∞Êï∞ÊçÆ');
				}
			});
		},
		
		// Âä†ËΩΩÁî®Êà∑ÁªüËÆ°Êï∞ÊçÆ
		loadUserStats() {
			const token = uni.getStorageSync('token');
			if (!token) return;
			
			// Ëé∑ÂèñËøêÂä®ÁªüËÆ°
			uni.request({
				url: 'http://localhost:8088/api/exercise-records/stats',
				method: 'GET',
				header: {
					'Authorization': 'Bearer ' + token
				},
				success: (res) => {
					if (res.data.code === 0) {
						const stats = res.data.data;
						this.userInfo.totalCalories = stats.totalCalories || 0;
						this.userInfo.totalMinutes = stats.totalMinutes || 0;
						this.userInfo.totalDays = stats.totalDays || 0;
					}
				},
				fail: () => {
					console.log('Âä†ËΩΩËøêÂä®ÁªüËÆ°Â§±Ë¥•');
				}
			});
		},
		
		// ‰∏ä‰º†Â§¥ÂÉè
		uploadAvatar(filePath) {
			const token = uni.getStorageSync('token');
			if (!token) {
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				});
				return;
			}
			
			uni.uploadFile({
				url: 'http://localhost:8088/api/users/avatar',
				filePath: filePath,
				name: 'avatar',
				header: {
					'Authorization': 'Bearer ' + token
				},
				success: (res) => {
					const data = JSON.parse(res.data);
					if (data.code === 0) {
						this.userInfo.avatar = data.data.avatarUrl;
						uni.setStorageSync('userInfo', this.userInfo);
						uni.showToast({
							title: 'Â§¥ÂÉè‰∏ä‰º†ÊàêÂäü',
							icon: 'success'
						});
					} else {
						uni.showToast({
							title: 'Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•',
							icon: 'none'
						});
					}
				},
				fail: () => {
					uni.showToast({
						title: 'Â§¥ÂÉè‰∏ä‰º†Â§±Ë¥•',
						icon: 'none'
					});
				}
			});
		},
		
		changeAvatar() {
			uni.chooseImage({
				count: 1,
				sizeType: ['compressed'],
				sourceType: ['album', 'camera'],
				success: (res) => {
					const tempFilePath = res.tempFilePaths[0];
					// ÂÖàÊòæÁ§∫ÈÄâ‰∏≠ÁöÑÂõæÁâá
					this.userInfo.avatar = tempFilePath;
					// ÁÑ∂Âêé‰∏ä‰º†Âà∞ÊúçÂä°Âô®
					this.uploadAvatar(tempFilePath);
				},
				fail: () => {
					uni.showToast({
						title: 'ÈÄâÊã©ÂõæÁâáÂ§±Ë¥•',
						icon: 'none'
					});
				}
			});
		},
		editInfo(type) {
			const currentValue = this.userInfo[type];
			const placeholder = type === 'age' || type === 'height' || type === 'weight' ? 
				`ÂΩìÂâçÂÄº: ${currentValue}` : `ËØ∑ËæìÂÖ•${this.getFieldName(type)}`;
			
			uni.showModal({
				title: 'ÁºñËæë' + this.getFieldName(type),
				editable: true,
				placeholderText: placeholder,
				success: (res) => {
					if (res.confirm && res.content) {
						let newValue = res.content;
						
						// Êï∞ÊçÆÁ±ªÂûãËΩ¨Êç¢
						if (type === 'age' || type === 'height' || type === 'weight') {
							newValue = parseFloat(newValue);
							if (isNaN(newValue)) {
								uni.showToast({
									title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÊï∞Â≠ó',
									icon: 'none'
								});
								return;
							}
						}
						
						// Êõ¥Êñ∞Êú¨Âú∞Êï∞ÊçÆ
						this.userInfo[type] = newValue;
						
						// ‰øùÂ≠òÂà∞ÂêéÁ´Ø
						this.saveUserInfo();
						
						uni.showToast({
							title: 'Êõ¥Êñ∞ÊàêÂäü',
							icon: 'success'
						});
					}
				}
			});
		},
		editGoal(type) {
			const goalNames = {
				calorie: 'ÊØèÊó•ÁÉ≠ÈáèÁõÆÊ†á',
				exercise: 'ÊØèÊó•ËøêÂä®Êó∂Èïø',
				steps: 'ÊØèÊó•Ê≠•Êï∞ÁõÆÊ†á'
			};
			
			const currentValue = type === 'calorie' ? this.userInfo.dailyCalorieGoal :
								type === 'exercise' ? this.userInfo.dailyExerciseGoal :
								this.userInfo.dailyStepGoal;
			
			uni.showModal({
				title: 'ÁºñËæë' + goalNames[type],
				editable: true,
				placeholderText: `ÂΩìÂâçÂÄº: ${currentValue}`,
				success: (res) => {
					if (res.confirm && res.content) {
						const value = parseInt(res.content);
						if (!isNaN(value) && value > 0) {
							// Êõ¥Êñ∞Êú¨Âú∞Êï∞ÊçÆ
							if (type === 'calorie') {
								this.userInfo.dailyCalorieGoal = value;
							} else if (type === 'exercise') {
								this.userInfo.dailyExerciseGoal = value;
							} else if (type === 'steps') {
								this.userInfo.dailyStepGoal = value;
							}
							
							// ‰øùÂ≠òÂà∞ÂêéÁ´Ø
							this.saveUserGoals();
							
							uni.showToast({
								title: 'ÁõÆÊ†áÊõ¥Êñ∞ÊàêÂäü',
								icon: 'success'
							});
						} else {
							uni.showToast({
								title: 'ËØ∑ËæìÂÖ•ÊúâÊïàÊï∞Â≠ó',
								icon: 'none'
							});
						}
					}
				}
			});
		},
		// ‰øùÂ≠òÁî®Êà∑‰ø°ÊÅØÂà∞ÂêéÁ´Ø
		saveUserInfo() {
			const token = uni.getStorageSync('token');
			if (!token) return;
			
			const userData = {
				name: this.userInfo.name,
				age: this.userInfo.age,
				height: this.userInfo.height,
				weight: this.userInfo.weight,
				gender: this.userInfo.gender
			};
			
			uni.request({
				url: 'http://localhost:8088/api/users/me',
				method: 'PUT',
				data: userData,
				header: {
					'Authorization': 'Bearer ' + token,
					'Content-Type': 'application/json'
				},
				success: (res) => {
					if (res.data.code === 0) {
						// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
						uni.setStorageSync('userInfo', this.userInfo);
						console.log('Áî®Êà∑‰ø°ÊÅØ‰øùÂ≠òÊàêÂäü');
					} else {
						console.log('Áî®Êà∑‰ø°ÊÅØ‰øùÂ≠òÂ§±Ë¥•:', res.data.message);
					}
				},
				fail: () => {
					console.log('Áî®Êà∑‰ø°ÊÅØ‰øùÂ≠òÂ§±Ë¥•');
				}
			});
		},
		
		// ‰øùÂ≠òÁî®Êà∑ÁõÆÊ†áÂà∞ÂêéÁ´Ø
		saveUserGoals() {
			const token = uni.getStorageSync('token');
			if (!token) return;
			
			const goals = {
				dailyCalorieGoal: this.userInfo.dailyCalorieGoal,
				dailyExerciseGoal: this.userInfo.dailyExerciseGoal,
				dailyStepGoal: this.userInfo.dailyStepGoal
			};
			
			uni.request({
				url: 'http://localhost:8088/api/users/goals',
				method: 'PUT',
				data: goals,
				header: {
					'Authorization': 'Bearer ' + token,
					'Content-Type': 'application/json'
				},
				success: (res) => {
					if (res.data.code === 0) {
						// ‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®
						uni.setStorageSync('userInfo', this.userInfo);
						console.log('Áî®Êà∑ÁõÆÊ†á‰øùÂ≠òÊàêÂäü');
					} else {
						console.log('Áî®Êà∑ÁõÆÊ†á‰øùÂ≠òÂ§±Ë¥•:', res.data.message);
					}
				},
				fail: () => {
					console.log('Áî®Êà∑ÁõÆÊ†á‰øùÂ≠òÂ§±Ë¥•');
				}
			});
		},
		
		getFieldName(type) {
			const names = {
				name: 'ÂßìÂêç',
				age: 'Âπ¥ÈæÑ',
				height: 'Ë∫´È´ò',
				weight: '‰ΩìÈáç',
				gender: 'ÊÄßÂà´'
			};
			return names[type] || '';
		},
		toggleSetting(type) {
			this.appSettings[type] = !this.appSettings[type];
		},
		onNotificationChange(e) {
			this.appSettings.notifications = e.detail.value;
			uni.showToast({
				title: this.appSettings.notifications ? 'ÈÄöÁü•Â∑≤ÂºÄÂêØ' : 'ÈÄöÁü•Â∑≤ÂÖ≥Èó≠',
				icon: 'none'
			});
		},
		onPrivacyChange(e) {
			this.appSettings.privacy = e.detail.value;
			uni.showToast({
				title: this.appSettings.privacy ? 'ÈöêÁßÅ‰øùÊä§Â∑≤ÂºÄÂêØ' : 'ÈöêÁßÅ‰øùÊä§Â∑≤ÂÖ≥Èó≠',
				icon: 'none'
			});
		},
		goToPage(page) {
			if (page === 'about') {
				uni.navigateTo({
					url: '/pages/about/about'
				});
			} else if (page === 'feedback') {
				uni.navigateTo({
					url: '/pages/feedback/feedback'
				});
			}
		},
		goToHealthProfile() {
			uni.navigateTo({
				url: '/pages/HealthProfile/HealthProfile'
			});
		},
		logout() {
			uni.showModal({
				title: 'Á°ÆËÆ§ÈÄÄÂá∫',
				content: 'Á°ÆÂÆöË¶ÅÈÄÄÂá∫ÁôªÂΩïÂêóÔºü',
				success: (res) => {
					if (res.confirm) {
						// Ê∏ÖÈô§Êú¨Âú∞Â≠òÂÇ®
						uni.removeStorageSync('token');
						uni.removeStorageSync('userInfo');
						
						// Ë∑≥ËΩ¨Âà∞ÁôªÂΩïÈ°µÈù¢
						uni.reLaunch({
							url: '/pages/login/login'
						});
						
						uni.showToast({
							title: 'Â∑≤ÈÄÄÂá∫ÁôªÂΩï',
							icon: 'success'
						});
					}
				}
			});
		}
	}
};
</script>

<style scoped>
